import React, {Dispatch, useRef, useState} from "react";
import {Button, Card, Space, Tabs} from "antd";
import {LoginForm, ProFormInstance, ProFormText} from "@ant-design/pro-form";
import {LockOutlined, UserOutlined,} from '@ant-design/icons';
import ThirdPartyLoginSDUCAS from "./ThirdPartyLoginSDUCAS";
import {withTranslation} from "react-i18next";
import {connect} from "react-redux";
import {withRouter} from "react-router";
import {userLoginTodo} from "../../Redux/Action/user";
import {loginInfo} from "../../Type/types";
import Logo from "Assert/img/sduoj.png"
import ForgetPass from "./Form/ForgetPass";

type LoginType = 'SDUCAS' | 'account';


const Login = (props: any) => {

    const formRef = useRef<ProFormInstance>()
    const [loginType, setLoginType] = useState<LoginType>("SDUCAS")

    return (
        <Card
            style={{width: "400px", textAlign: "center", margin: "0 auto"}}>
            <LoginForm
                formRef={formRef}
                logo={Logo}
                title=""
                subTitle=""
                actions={
                    <></>
                }
                submitter={{
                    resetButtonProps: false,
                    render: (prop, def) => {
                        if (loginType !== 'SDUCAS')
                            return <Button type={"primary"} block onClick={() => {
                                formRef.current?.validateFieldsReturnFormatValue?.()?.then((value) => {
                                    props.login(value)
                                })
                            }
                            }> {props.t("Login")} </Button>
                        return <ThirdPartyLoginSDUCAS/>
                    },
                }}
            >
                <Tabs activeKey={loginType} onChange={(activeKey) => setLoginType(activeKey as LoginType)}>
                    <Tabs.TabPane key={'account'} tab={''}/>
                    <Tabs.TabPane key={'SDUCAS'} tab={''}/>
                </Tabs>
                {loginType === 'account' && (
                    <>
                        <ProFormText
                            name="username"
                            fieldProps={{
                                size: 'large',
                                prefix: <UserOutlined className={'prefixIcon'}/>,
                                onPressEnter: () => {
                                    formRef.current?.validateFieldsReturnFormatValue?.()?.then((value) => {
                                        props.login(value)
                                    })
                                }
                            }}
                            placeholder={''}
                            rules={[
                                {
                                    required: true,
                                    message: '!',
                                },
                            ]}

                        />
                        <ProFormText.Password
                            name="password"
                            fieldProps={{
                                size: 'large',
                                prefix: <LockOutlined className={'prefixIcon'}/>,
                                onPressEnter: () => {
                                    formRef.current?.validateFieldsReturnFormatValue?.()?.then((value) => {
                                        props.login(value)
                                    })
                                }
                            }}
                            placeholder={''}
                            rules={[
                                {
                                    required: true,
                                    message: '',
                                },
                            ]}
                        />
                    </>
                )}
                {
                    loginType === 'account' && (
                        <div style={{
                            textAlign: "right", marginBottom: 10
                        }}>
                            <Space size={3}>
                                {/*<Register button={<Button type={"link"} size={"small"}></Button>}/>*/}
                                <ForgetPass button={<Button type={"link"} size={"small"}></Button>}/>
                            </Space>
                        </div>
                    )
                }
            </LoginForm>
        </Card>
    )
}


const mapStateToProps = (state: any) => {
    return {}
}

const mapDispatchToProps = (dispatch: Dispatch<any>) => ({
    login: (data: loginInfo) => dispatch(userLoginTodo(data))
})

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(
    withTranslation()(
        withRouter(Login)
    ))
